.alert-sms {
  width: 300px;
  height: 60px;
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 128, 0, .8);
  border-radius: 10px;
  padding: 20px;
  transition: transform .5s linear;
  position: absolute;
  top: 60px;
  left: 0;
  transform: translateX(-100%);
}

.is-visible-sms {
  transform: translateX(0%);
}

.clockface {
  margin-left: 20px;
  font-size: 30px;
  font-weight: 600;
}

.actions {
  margin-bottom: 10px;
  padding-left: 10px;
}

.timer-btn {
  text-transform: uppercase;
  color: #fff;
  background-color: #5722b4;
  border: none;
  border-radius: 3px;
  margin-right: 10px;
  padding: 5px 15px;
}

